import { useEffect, useState } from "react";
import { Modal } from "react-bootstrap";
import { useTranslation } from "react-i18next";
import StickyBox from "react-sticky-box";
import Alert from "@mui/material/Alert";
import CircularProgress from "@mui/material/CircularProgress";
import DashboardSidebar from "client/components/dashboard/sidebar/sidebar.jsx";
import { CityService } from "admin/services";
import { DataTable } from "common/components";
import { PageMode, TableInputType } from "common";
import AddUpdateForm from "./AddUpdateForm";

const CitiesList = () => {
  const [t] = useTranslation();
  const [loading, setLoading] = useState(false);
  const [deleting, setDeleting] = useState(false);
  const [show, setShow] = useState(false);
  const [showDelete, setShowDelete] = useState(false);
  const [showSuccess, setShowSuccess] = useState(false);
  const [message, setMessage] = useState();
  const [messageDelete, setMessageDelete] = useState();
  const [citiesData, setCitiesData] = useState();
  const [selectedCity, setSelectedCity] = useState(null);
  const [mode, setMode] = useState(PageMode.Add);
  const handleClose = () => setShow(false);
  const handleShow = () => {
    setShow(true);
    setShowSuccess(false);
  };
  const handleCloseDelete = () => setShowDelete(false);
  const handleShowDelete = () => {
    setShowDelete(true);
    setShowSuccess(false);
  };

  useEffect(async () => {
    setLoading(true);
    const response = await CityService.getAllCities();
    setMessage(!!response?.message && response?.message);
    if (response.success) {
      setCitiesData(
        response.body?.map((s) => {
          return {
            ...s,
            countryName: s.country.name,
            countryId: s.country.id,
          };
        })
      );
    }

    setLoading(false);
  }, []);

  const deleteCityHandler = async (id) => {
    setDeleting(true);

    const response = await CityService.deleteCity(id);

    setDeleting(false);
    if (!!!response.success && !response.success) {
      setMessageDelete(!!response.message && response.message);
      return;
    }
    handleCloseDelete();
    setCitiesData(citiesData.filter((u) => u.id != id));

    if (!!!response.success && !response.success) {
      setMessage(!!response?.message && response?.message);
      // To-Do: implemet error msg
      setLoading(false);
      return;
    }
    setLoading(false);
  };
  const updateCityStatusHandler = async (id, active) => {
    setLoading(true);
    let requestData = new FormData();
    const cityData = {
      active: !active,
    };
    requestData.append("request", JSON.stringify(cityData, 0, 2));
    const response = await CityService.updateCity(id, requestData);

    if (!!!response.success && !response.success) {
      // To-Do: implemet error msg
      setMessage(!!response?.message && response?.message);
      setLoading(false);
      return;
    }

    setCitiesData(
      citiesData?.map((s) => {
        return s.id === response?.body?.id
          ? { ...s, active: response?.body?.active }
          : s;
      })
    );
    setLoading(false);
  };
  const columns = [
    { id: "countryName", label: t("City.Country") },
    { id: "name", label: t("City.Name") },
    { id: "active", label: t("City.Active"), type: TableInputType.Toggle },
  ];
  let tableContent = (
    <DataTable
      columns={columns}
      rows={citiesData}
      showActions={true}
      onEdit={(city) => {
        handleShow();
        setSelectedCity(city);
        setMode(PageMode.Update);
      }}
      onDelete={(city) => {
        handleShowDelete();
        setSelectedCity(city);
        setMessageDelete(null);
      }}
      onUpdateStatus={(city) => {
        updateCityStatusHandler(city.id, city.active);
      }}
    />
  );

  return (
    <div>
      <div className="content">
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-5 col-lg-4 col-xl-3 theiaStickySidebar">
              <StickyBox offsetTop={20} offsetBottom={20}>
                <DashboardSidebar />
              </StickyBox>
            </div>
            <div className="col-md-7 col-lg-8 col-xl-9">
              <div className="row">
                <div className="col-sm-12">
                  <div className="card">
                    <div className="card-header">
                      <h4 className="card-title float-left">
                        {t("City.ListTitle")}
                      </h4>
                      <button
                        className="btn btn-primary float-right"
                        onClick={() => {
                          handleShow();
                          setSelectedCity(null);
                          setMode(PageMode.Add);
                        }}
                        data-toggle="modal"
                      >
                        {t("City.Add")}
                      </button>
                    </div>
                    <div className="card-body ">
                      <div className="card card-table mb-0">
                        <div className="card-body">
                          <div className="table-responsive">
                            <div className="invalid-feedback d-block mb-2">
                              {message}
                            </div>
                            {loading ? (
                              <div className="centered">
                                <h4>
                                  <div>
                                    <CircularProgress />
                                  </div>
                                  <div>
                                    <span style={{ color: "#15558d" }}>
                                      {t("City.GettingCities")}
                                    </span>
                                  </div>
                                </h4>
                              </div>
                            ) : (
                              <>
                                {showSuccess ? (
                                  <div style={{ margin: "20px" }}>
                                    <Alert severity="success">
                                      {mode === PageMode.Add
                                        ? t("City.AddingSuccessfullyMessage")
                                        : t("City.UpdatingSuccessfullyMessage")}
                                    </Alert>
                                  </div>
                                ) : (
                                  <></>
                                )}
                                {tableContent}
                              </>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal
        show={show}
        onHide={handleClose}
        size="lg"
        scrollable={true}
        aria-labelledby="example-modal-sizes-title-sm"
      >
        <Modal.Header>
          <h5 className="modal-title">{`${t(mode)} ${t("City.City")}`}</h5>
          <button
            type="button"
            className="close"
            data-dismiss="modal"
            aria-label="Close"
            onClick={handleClose}
          >
            <span aria-hidden="true">√ó</span>
          </button>
        </Modal.Header>
        <Modal.Body>
          <div className="modal-body">
            <div>
              <AddUpdateForm
                selectedCity={selectedCity}
                mode={mode}
                onClose={(city) => {
                  handleClose();
                  if (city) {
                    console.log(
                      "üöÄ ~ file: index.jsx ~ line 204 ~ CitiesList ~ city",
                      city
                    );
                    if (mode === PageMode.Update) {
                      const updatedCity = citiesData.map((u) => {
                        return u.id === city.id
                          ? {
                              ...city,
                              countryName: city.country.name,
                              countryId: city.country.id,
                            }
                          : u;
                      });
                      setCitiesData(updatedCity);
                    } else {
                      const addedCounrtry = {
                        ...city,
                        countryName: city.country.name,
                        countryId: city.country.id,
                      };
                      setCitiesData([...citiesData, addedCounrtry]);
                    }
                    setShowSuccess(true);
                  }
                }}
              />
            </div>
          </div>
        </Modal.Body>
      </Modal>

      <Modal
        show={showDelete}
        onHide={handleCloseDelete}
        aria-labelledby="example-modal-sizes-title-sm"
        centered
      >
        <Modal.Header>
          <h5 className="modal-title">{t("City.DeleteConfirmationTitle")}</h5>
          <button
            type="button"
            className="close"
            data-dismiss="modal"
            aria-label="Close"
            onClick={handleCloseDelete}
          >
            <span aria-hidden="true">√ó</span>
          </button>
        </Modal.Header>
        <Modal.Body>
          <div className="modal-body">
            {messageDelete && (
              <div className="invalid-feedback d-block mb-2">
                {messageDelete}
              </div>
            )}
            <h5 style={{ color: "red" }}>
              {t("City.DeleteConfirmationMessage")}
            </h5>
            <div>
              <b>{t("City.City")}:</b>
              {` ${selectedCity?.name} `}
            </div>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button
            className="btn btn-danger submit-btn"
            disabled={deleting}
            onClick={() => {
              deleteCityHandler(selectedCity.id);
            }}
          >
            {!deleting ? t(`City.Delete`) : ""}
            {deleting && (
              <span className="spinner-border spinner-border-sm mb-1"></span>
            )}
          </button>
          <button
            className="btn btn-secondary submit-btn"
            onClick={handleCloseDelete}
          >
            {t("City.Close")}
          </button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}
export default CitiesList;
